  top: -4px;
  font-size: 12px;
  padding-left: 2px;
}
.catalog-filter .catalog-section-main {
  width: 100%;
  font-size: 20px;
  padding: 5px 0;
  color: var(--dark);
  cursor: pointer;
  display: block;
  position: relative;
  padding-right: 40px;
}
.catalog-filter .catalog-section-main::after {
  position: absolute;
  top: 40%;
  right: 10px;
  transform: translateY(-50%);
  content: "";
  width: 30px;
  height: 30px;
  background-image: url("../assets/img/icons/catalog-filter-arrow-grey.svg");
  background-size: 24px;
  background-position: center;
  transition: transform var(--main-transition);
}
